* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

html {
  font-size: 62.5%;
}

body {
  margin: 0 auto !important;
  position: relative;
  font-family: "Nunito", sans-serif;
  font-size: 16px;
  background-color: #f3f3f3;
  min-width: 320px;
  max-width: 1920px;
}

img {
  max-width: 100%;
}

li {
  list-style: none;
}

a, a:hover {
  text-decoration: none;
}

.contenedor {
  margin: 1rem auto;
  padding: 1rem 0;
  max-width: 1920px;
  position: relative;
}

.header-home {
  background: transparent;
  position: absolute;
  width: 100%;
  z-index: 10;
}
.header-home .mobile-menu {
  position: absolute;
  height: 100vh;
  top: 0;
  bottom: 0;
  right: 0;
  width: 0%;
  overflow: hidden;
  display: flex;
  align-items: center;
  justify-content: center;
  color: #fff;
  background-color: #1f1e53e3;
  opacity: 0;
  transition: all 0.3s;
}
.header-home .mobile-menu ul {
  display: flex;
  flex-direction: column;
  gap: 3rem;
}
.header-home .mobile-menu ul li {
  font-size: 3rem;
}
.header-home .active {
  opacity: 1;
  width: 100%;
}

.contenedor-header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 10px;
  gap: 1rem;
}
.contenedor-header .logo {
  width: 30%;
}

.iconos-header .mobile {
  position: relative;
  z-index: 1;
}
.iconos-header i {
  cursor: pointer;
  color: white;
  font-size: 2.2rem;
}

.contact-phone {
  font-weight: 700;
  padding: 0.6rem 1rem;
  border: 1px solid;
  border-radius: 5px;
  color: #fff;
}
.contact-phone:hover {
  background-color: #fff;
  border: 0;
}

.user-account-icon {
  display: none;
}

.tablet {
  display: none;
}

nav.desktop {
  display: none;
}
nav.desktop ul {
  display: flex;
  justify-content: center;
  gap: 2rem;
  margin-top: 1rem;
  width: 70%;
}
nav.desktop li {
  padding: 3px 12px;
  opacity: 0.8;
  transition: all 0.2s;
}
nav.desktop a {
  color: #fff;
  opacity: 0.8;
}
nav.desktop a:hover {
  opacity: 1;
}

nav.desktop ul li:nth-last-child(1) a {
  padding: 3px 12px;
  background-color: dodgerblue;
  border-radius: 6px 0 6px 0;
  color: #fff;
}

@media (min-width: 769px) {
  .header-home {
    max-width: 1920px;
    width: 100%;
    z-index: 10;
    position: absolute;
  }

  .contenedor-header {
    width: 90%;
    margin: 0 auto;
  }

  .logo .logo-mobile {
    display: none;
  }

  .logo .tablet {
    display: block;
    width: 160px;
  }

  .iconos-header {
    gap: 4.5rem;
  }

  .user-account-icon {
    display: block;
  }

  .mobile {
    display: none;
  }
}
@media (min-width: 992px) {
  nav.desktop {
    display: flex;
    justify-content: center;
  }
}
.property-filter {
  border: 1px solid white;
  margin: 0 auto;
  background: linear-gradient(45deg, #394253, #000000c2);
}
.property-filter h3 {
  color: #fff;
  padding: 1rem;
  text-align: center;
}
.property-filter .more-options {
  display: flex;
  align-items: center;
  justify-content: center;
  color: #fff;
  gap: 1rem;
  margin: 1rem;
  padding: 0.5rem;
  text-align: center;
  grid-column: 1/3;
  cursor: pointer;
}

.search-form {
  display: grid;
  grid-template-columns: repeat(2, 1fr);
}
.search-form button {
  padding: 1rem 0;
  width: 100%;
  border: 0;
  background-color: #6495eda8;
  color: #fff;
  font-size: 2rem;
  display: flex;
  justify-content: center;
  align-items: center;
  gap: 2rem;
  border-radius: 5px;
}

.search--form-field {
  padding: 2rem 1rem;
  border-top: 1px solid #80808047;
  margin-bottom: 5px;
}
.search--form-field:last-child {
  border-bottom: 1px solid #80808047;
}
.search--form-field label {
  display: block;
  font-weight: 700;
  color: #ede7e7;
}
.search--form-field select,
.search--form-field input {
  padding: 1rem;
  width: 100%;
  border: 0;
  border-radius: 5px;
  background: #2e3035;
  color: #ede7e7;
}
.search--form-field p {
  color: #fff;
}

.adicionales {
  display: none;
  grid-template-columns: repeat(2, 1fr);
  grid-column: 1/3;
  transition: all 0.3s;
}

.mostrar {
  display: grid;
}

@media (min-width: 769px) {
  .property-filter {
    background: linear-gradient(45deg, #000, transparent);
    border: 0;
    bottom: 60px;
    width: 80%;
    position: absolute;
    left: 0;
    right: 0;
    margin-left: auto;
    margin-right: auto;
    min-height: 150px;
  }
  .property-filter .more-options {
    grid-column: 1/5;
  }

  .search-form {
    display: grid;
    grid-template-columns: repeat(4, 1fr);
    gap: 2rem;
  }
  .search-form button {
    padding: 0.7rem 1rem;
    width: auto;
  }
  .search-form .search--form-field {
    padding: 0 1rem;
    border: 0;
  }
  .search-form .search--form-field option {
    color: #000;
  }

  .adicionales {
    grid-template-columns: repeat(4, 1fr);
    grid-column: 1/5;
  }
}
.desktop {
  display: none;
}

@media (min-width: 481px) {
  .most-featured-properties {
    grid-template-columns: repeat(2, 1fr);
  }
  .most-featured-properties .first {
    grid-column: 1/3;
  }
}
@media (min-width: 769px) {
  .desktop {
    display: grid;
  }

  .most-featured-properties {
    grid-template-columns: repeat(3, 1fr);
  }
  .most-featured-properties .first {
    grid-column: 1/2;
  }

  .banner-home1 span {
    width: 70%;
    font-size: 3rem;
    font-weight: bold;
  }

  .contenedor {
    width: 80%;
  }
}
.slide {
  background: linear-gradient(45deg, black, transparent), url("../images/slide-one.jpg");
  overflow: hidden;
  height: 100vh;
  background-size: cover;
  background-repeat: no-repeat;
  background-position: center;
  position: relative;
}

.h3-home {
  text-align: center;
}
.h3-home h3 {
  margin: 10px 0 5px;
  font-weight: 200;
  font-size: 1.9rem;
}
.h3-home .separador {
  width: 45px;
  color: #8080808c;
  margin: 0 auto;
  border: 2px solid;
  border-radius: 2px;
}

.banner-home1 {
  background: linear-gradient(45deg, black, transparent), url("../images/banner1.webp");
  overflow: hidden;
  background-size: cover;
  background-repeat: no-repeat;
  background-position: center;
  background-attachment: fixed;
  height: 480px;
  display: flex;
  align-items: center;
  justify-content: center;
}
.banner-home1 span {
  padding: 0 3rem;
  color: #fff;
  text-align: center;
}

.most-featured-properties {
  display: grid;
  gap: 1rem;
  padding: 2rem 0;
}

.property {
  position: relative;
  background-color: #fff;
  box-shadow: -1px 1px 8px 0px rgba(0, 0, 0, 0.25);
  border-radius: 4px;
  padding-bottom: 1rem;
  overflow: hidden;
  cursor: pointer;
}
.property h3 {
  text-align: center;
  font-weight: 700;
  padding: 1rem 0;
}
.property .ref {
  margin: 0;
  margin-left: 10px;
  color: grey;
}
.property .address {
  padding: 0.5rem 1rem;
  color: #0073bd;
}
.property .info {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
  align-items: center;
  padding: 0 1rem;
}
.property .info .price {
  display: flex;
  align-items: center;
  flex-wrap: wrap;
  gap: 1rem;
  color: #0073bd;
  font-weight: 700;
}
.property .info .price .currency,
.property .info .price .finalPrice {
  font-size: 2rem;
}
.property .info .price .consultarPrecio {
  font-size: 2rem;
  line-height: 15px;
  margin: 1rem 0;
}
.property .info .price .fa-whatsapp {
  font-size: 2.5rem;
}
.property .operationType {
  position: absolute;
  border-radius: 3px;
  color: #fff;
  font-weight: 700;
  padding: 0 0.2rem;
  left: 5px;
  top: 5px;
  display: inline-table;
  z-index: 9999;
  font-size: 1.5rem;
  line-height: 18px;
}
.property .propertyType {
  position: absolute;
  bottom: 0;
  left: 5px;
  z-index: 9999;
  color: #fff;
  font-weight: 700;
  font-size: 1.2rem;
}
.property .fa-bookmark {
  position: absolute;
  right: 10px;
  top: 0;
  z-index: 110;
  color: #ffc100;
  font-size: 3rem;
}

.features {
  display: flex;
  justify-content: space-between;
  padding: 1rem;
}
.features .feature {
  display: flex;
  gap: 0.4rem;
}
.features .feature p {
  font-weight: 700;
}
.features .feature i {
  font-size: 2.5rem;
  color: #000000a1;
}

.property-image {
  position: relative;
  transition: all 0.3s;
  overflow: hidden;
}
.property-image .gradient {
  position: absolute;
  top: 0;
  bottom: 0;
  width: 100%;
  z-index: 100;
  background: linear-gradient(179deg, #00000099, #00000026);
}
.property-image img {
  width: 100%;
  transition: all 0.5s;
}
.property-image:hover img {
  transform: scale(1.1);
  overflow: hidden;
}

@media (min-width: 769px) {
  .property h3 {
    text-align: left;
    margin-left: 1rem;
  }
  .property .propertyType, .property .operationType {
    font-size: 1.4rem;
  }
}
@media (min-width: 992px) {
  .property .operationType {
    right: 5px;
    bottom: 5px;
    top: auto;
    left: auto;
  }
}
.tag-alquiler {
  background-color: #ff3c01ed;
}

.tag-venta {
  background-color: #0297dfed;
}

.financiacion {
  letter-spacing: 0.5px;
  font-size: 1rem;
  padding: 0.2rem 0.5rem;
  margin-right: 5px;
  background-color: #ff0000e6;
  color: #fff;
  border-radius: 10px;
  text-align: center;
}

.lista-propiedades {
  margin-top: 2rem;
  margin-bottom: 5rem;
  padding: 0.3rem;
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  gap: 0.5rem;
}

@media (min-width: 769px) {
  .lista-propiedades {
    grid-template-columns: repeat(3, 1fr);
  }
}
@media (min-width: 992px) {
  .lista-propiedades {
    grid-template-columns: repeat(4, 1fr);
    gap: 1.2rem;
  }
}
.form-createProduct fieldset {
  border: 1px solid #8080805c;
}
.form-createProduct legend {
  display: inline;
  width: auto;
  padding: 0 1rem !important;
}

.grid-2 {
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  gap: 2rem;
}

.price-fields {
  display: flex;
  align-items: end;
  gap: 1rem;
}

.grid-3 {
  display: grid;
  grid-template-columns: repeat(3, 30%);
  gap: 2rem;
}

.label-form {
  padding: 0 1rem;
}
.label-form input {
  margin-right: 1rem;
}

.header-property--detail {
  background-color: #344554;
}

.property-detail {
  padding: 120px 0;
}
.property-detail h1 {
  font-weight: bold;
  padding: 1rem;
}
.property-detail h2 {
  text-align: center;
  text-transform: uppercase;
  font-weight: 100;
}
.property-detail .buttons {
  display: flex;
  justify-content: center;
  gap: 1rem;
  padding: 1rem;
}
.property-detail .buttons button {
  border: 1px solid gray;
  padding: 0.2rem 1rem;
  border-radius: 5px;
  background-color: #7b00b9;
  color: white;
  box-shadow: 1px 0px 7px 4px #00000052;
}
.property-detail .informacion {
  grid-column: 2/3;
  grid-row: 1/2;
  padding: 1rem;
}
.property-detail .informacion div {
  display: flex;
  justify-content: space-between;
  align-items: center;
  border-bottom: 1px solid #8080804d;
  padding: 0.2rem 0.5rem;
}
.property-detail .informacion div p {
  margin: 0;
  padding: 0;
}
.property-detail .informacion span {
  font-weight: bold;
}
.property-detail .informacion .badge-detail {
  font-size: 1rem;
  letter-spacing: 0.5px;
  margin-right: 5px;
  padding: 0.2rem 0.5rem;
  text-align: center;
  text-transform: uppercase;
}
.property-detail .property-detail--info .descripcion {
  padding: 1rem;
}
.property-detail .features-container {
  border: 1px solid #80808085;
  border-radius: 5px;
  padding: 1rem 0.5rem;
}
.property-detail .propertyFeatures div {
  display: flex;
  align-items: center;
  justify-content: space-between;
}
.property-detail .descripcion {
  padding: 1rem 0;
}

@media (min-width: 769px) {
  .property-detail {
    width: 90%;
    margin: 0 auto;
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    gap: 2rem;
  }
  .property-detail .buttons {
    display: none;
  }
  .property-detail .property-detail--title {
    grid-column: 2/3;
    grid-row: 1/2;
    font-size: 3rem;
  }
  .property-detail .informacion {
    margin-top: 6rem;
  }
}
.search-results--main {
  min-height: 100vh;
}

.search-results-container {
  padding-top: 150px;
  margin: 0 auto;
}
.search-results-container h1 {
  font-size: 3rem;
}
.search-results-container .lista-propiedades {
  padding-bottom: 5rem;
}

@media (min-width: 769px) {
  .search-results-container {
    width: 80%;
  }
}
.footer {
  margin-top: 10rem;
  height: 221px;
  background-color: #344554;
  text-align: center;
  position: relative;
}
.footer .wave1 {
  background-image: url(/images/wave_344554.png);
  height: 100px;
  position: absolute;
  top: -96px;
  left: 0;
  width: 100%;
}
.footer .social-icons {
  display: flex;
  gap: 1.6rem;
  justify-content: center;
  padding: 30px;
  font-size: 3rem;
}
.footer .social-icons a {
  color: #fff;
  transition: all 0.5s;
}
.footer .social-icons a:hover {
  transform: translateY(-10px);
}
.footer .nav-footer ul {
  display: none;
  align-items: center;
  justify-content: center;
  gap: 2rem;
}
.footer .nav-footer ul li a {
  color: #fff;
  opacity: 0.75;
}
.footer .nav-footer ul li a:hover {
  opacity: 1;
}
.footer .copy {
  color: #fff;
}

@media (min-width: 769px) {
  .nav-footer ul {
    display: flex;
  }
}
.header-admin {
  display: grid;
  grid-template-columns: repeat(3, 33%);
  grid-template-rows: repeat(2, 1fr);
  align-items: center;
  gap: 0.5rem;
  padding: 1rem;
}

.mobile {
  font-size: 3rem;
  cursor: pointer;
  color: black;
}

.mostrarMenu {
  width: 200px !important;
  transition: width 0.2s;
}

.barra-lateral {
  width: 0;
  overflow: hidden;
  position: fixed;
  top: 0;
  left: 0;
  height: 100vh;
  background-color: #344554;
  color: #fff;
  transition: width 0.2s;
}
.barra-lateral a {
  color: #fff;
}
.barra-lateral ul {
  width: 100%;
}
.barra-lateral ul li {
  cursor: pointer;
  position: relative;
  font-size: 1.6rem;
  font-weight: 100;
  margin: 1rem 0;
  padding: 1rem;
}
.barra-lateral ul li:nth-child(1) {
  margin-top: 5rem;
  margin-bottom: 10rem;
  font-weight: bold;
}
.barra-lateral ul li:hover:not(:nth-child(1)) {
  background-color: #fff;
}
.barra-lateral ul li i {
  font-size: 2.3rem;
  margin-right: 2.6rem;
}

.barra-lateral ul li:hover a {
  color: #3586ff;
}

.header-admin h1 {
  font-size: 1.9rem;
  font-weight: bold;
}

.user {
  height: 50px;
  border: 1px solid black;
}

#buscar {
  width: 80%;
  padding: 1.2rem;
  border-radius: 15px;
  border: 0;
  margin-bottom: 2rem;
  box-shadow: inset 1px 4px 20px #eae4e4d9;
}

.contenedor-botones {
  width: 80%;
  margin: 0 auto;
}

.bntDashboard1 {
  border: 0;
  padding: 0.6rem 1rem;
  border-radius: 12px;
  background-color: #344554;
  color: #fff;
}

.btn-danger {
  background: #c12232db !important;
  border-color: #dc3545 !important;
}

button.eliminar-producto.bntDashboard1.btn-danger {
  padding: 0.9rem 1.1rem;
}

.contenedor {
  width: 90%;
  margin: 0 auto;
  max-width: 1200px;
}

.gridProductos {
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  gap: 1rem;
  padding: 1rem;
}

.productTable {
  display: none;
  width: 80%;
  margin: 0 auto;
  border-collapse: separate;
  border-spacing: 0 8px;
}

.productTable thead {
  background-color: aliceblue;
}

.productTable thead th {
  padding: 1rem 3rem;
  text-align: center;
}

.productTable tbody {
  background-color: #fff;
}

.productTable tbody tr:hover {
  background-color: #f6eeee;
}

.productTable tbody tr td {
  text-align: center;
  padding: 0 0.5rem;
}

.acciones {
  display: flex;
  gap: 0.5rem;
  justify-content: center;
  margin-top: 7px;
}

.producto-admin {
  margin: 0 0 4rem 0;
  border: 1px solid #80808057;
  background-color: #fff;
  border-radius: 5px;
  display: flex;
  flex-direction: column;
  gap: 1rem;
  padding-bottom: 1rem;
  border-radius: 20px;
  overflow: hidden;
}

.iconos-acciones {
  display: flex;
  justify-content: center;
  align-items: center;
  gap: 2rem;
}

i {
  pointer-events: none;
}

.info-producto-admin {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
  padding: 0 1rem;
}

.titulo-producto-admin {
  width: 100%;
  font-weight: bold;
  color: #2d2942;
  border-bottom: 1px solid #00000040;
}

.precio-producto-admin {
  font-weight: bold;
}

.enStock {
  color: green;
}

.sinStock {
  color: red;
}

.form-modal-edit {
  display: grid;
}

.input-shipping input,
.input-published input {
  width: auto !important;
}

.input-shipping,
.input-published {
  display: flex;
  gap: 1rem;
  align-items: center;
}

.input-featured {
  display: flex;
  align-items: center;
  gap: 1rem;
  padding: 0.8rem 0.5rem;
  background-color: var(--celeste);
  border-radius: 5px;
  color: #fff;
  font-weight: bold;
}

.input-featured label {
  margin: 0 !important;
}

.input-imagenes {
  margin: 1rem 0;
  padding: 1rem 0.5rem;
  border: 1px solid #80808047;
  overflow: hidden;
  border-radius: 5px;
}

.form-createProduct input[type=file] {
  padding: 1rem;
}

.form-modal-edit textarea {
  width: 100%;
  padding: 0.5rem;
}

.price-fields:nth-child(1),
.price-fields:nth-child(2) {
  flex: 50%;
}

@media (min-width: 481px) {
  .gridProductos {
    grid-template-columns: repeat(3, 1fr);
    gap: 1rem;
  }

  .info-producto-admin {
    justify-content: space-between;
  }

  .precio-producto-admin,
.sku-producto-admin,
.stock-producto-admin {
    width: auto;
  }
}
@media (min-width: 769px) {
  .gridProductos {
    grid-template-columns: repeat(4, 1fr);
  }

  .header-admin {
    padding: 1rem 6rem;
  }
}
@media (min-width: 992px) {
  .header-admin {
    grid-template-columns: 25% 50% 25%;
    grid-template-rows: auto;
    padding: 3rem;
  }

  .barra-lateral {
    display: flex;
    width: 200px;
  }

  .main-contenedor {
    margin-left: 200px;
  }

  .productTable {
    display: block;
  }

  .gridProductos {
    display: none;
  }

  .mobile {
    display: none;
  }

  .user {
    grid-column: 3/4;
  }

  #buscar {
    margin-bottom: 0;
  }
}
.form-createProduct {
  padding: 1rem;
  background: linear-gradient(45deg, #ff00d15c, #75fff9);
  max-width: 600px;
  margin: 2rem auto;
  border-radius: 10px;
  box-shadow: 8px 8px 50px 5px #80808069;
}

.form-createProduct input {
  max-width: 600px;
}

.form-createProduct label {
  font-weight: bold;
}

.form-createProduct input[type=text], input[type=number], textarea, select {
  padding: 1rem;
  margin: 0.5rem 0;
  width: 100%;
  background: transparent;
  border: 0;
  border-bottom: 1px solid #80808047;
}